@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;700&display=swap');

/* Reset and base */
* {
    box-sizing: border-box;
}
body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
    background: #f8f9fa;
    color: #333;
    line-height: 1.6;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}
a {
    text-decoration: none;
    color: inherit;
}

/* Nav bar */
nav {
    background: #5a2a83;
    color: white;
    padding: 0.75rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 20;
    box-shadow: 0 2px 8px rgba(49,17,80, 0.3);
}
nav .logo {
    font-weight: 700;
    font-size: 1.8rem;
    letter-spacing: 2px;
}
nav .search-container {
    flex-grow: 1;
    max-width: 400px;
    margin: 0 1.5rem 0 2rem;
    position: relative;
}
nav input[type="search"] {
    width: 100%;
    padding: 0.5rem 1rem;
    border-radius: 25px;
    border: none;
    font-size: 1rem;
    outline: none;
    transition: box-shadow 0.3s ease;
}
nav input[type="search"]:focus {
    box-shadow: 0 0 8px 2px #c293fc;
}

/* Cart button in navbar */
nav .cart-button {
    position: relative;
    background: transparent;
    border: none;
    cursor: pointer;
    color: white;
    font-size: 1.6rem;
    display: flex;
    align-items: center;
    padding: 0.3rem 0.5rem;
}
nav .cart-button svg {
    width: 24px;
    height: 24px;
}
nav .cart-button .cart-count {
    position: absolute;
    top: -6px;
    right: -6px;
    background: #ff4081;
    color: white;
    font-size: 0.75rem;
    font-weight: 700;
    border-radius: 50%;
    padding: 2px 6px;
    line-height: 1;
    user-select: none;
    pointer-events: none;
}
nav .cart-button:focus {
    outline-offset: 3px;
    outline: 2px solid #c293fc;
}

/* Main layout */
.container {
    flex: 1;
    display: flex;
    background: white;
    margin: 1.5rem 2rem;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 4px 15px rgba(90, 42, 131, 0.15);
}

/* Side carousel */
.side-carousel {
    width: 280px;
    background: #6c4aaf;
    color: white;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    align-items: center;
    position: relative;
}
.carousel-image {
    flex-shrink: 0;
    height: 300px;
    width: 100%;
    object-fit: cover;
    transition: transform 0.6s ease;
}
.carousel-controls {
    position: absolute;
    bottom: 12px;
    display: flex;
    justify-content: center;
    width: 100%;
}
.carousel-controls button {
    background: rgba(255 255 255 / 0.6);
    border: none;
    border-radius: 50%;
    width: 14px;
    height: 14px;
    margin: 0 6px;
    cursor: pointer;
}
.carousel-controls button.active {
    background: white;
}

/* Main content */
.main-content {
    flex: 1;
    padding: 1.5rem 2rem;
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
}

/* Category buttons */
.categories {
    display: flex;
    gap: 1rem;
    justify-content: center;
}
.categories button {
    background: #c8b3ff;
    border: none;
    border-radius: 25px;
    padding: 0.5rem 1.25rem;
    font-weight: 500;
    cursor: pointer;
    color: #4a2da1;
    transition: background-color 0.3s ease, color 0.3s ease;
    box-shadow: 0 2px 5px rgba(90, 42, 131, 0.2);
}
.categories button:hover,
.categories button.active {
    background: #5a2a83;
    color: white;
    box-shadow: 0 5px 15px #5a2a83aa;
}

/* Products grid */
.products {
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(220px,1fr));
    gap: 1.5rem;
}
.product-card {
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(90, 42, 131, 0.1);
    padding: 1rem;
    background: white;
    display: flex;
    flex-direction: column;
    transition: transform 0.25s ease, box-shadow 0.25s ease;
}
.product-card:hover {
    transform: translateY(-6px);
    box-shadow: 0 5px 25px #5a2a83cc;
}
.product-card img {
    width: 100%;
    border-radius: 12px;
    object-fit: cover;
    max-height: 180px;
    margin-bottom: 0.75rem;
}
.product-card h3 {
    margin: 0 0 0.3rem 0;
    font-weight: 700;
    color: #5a2a83;
}
.product-card p {
    font-size: 0.9rem;
    flex-grow: 1;
    color: #555;
}
.product-card .price {
    margin: 0.75rem 0;
    font-weight: 700;
    color: #8e44ad;
}
.product-card button.add-to-cart {
    background: #5a2a83;
    color: white;
    border: none;
    padding: 0.5rem;
    border-radius: 25px;
    cursor: pointer;
    font-weight: 600;
    transition: background 0.2s ease;
}
.product-card button.add-to-cart:hover {
    background: #7f50be;
}

/* Cart panel as floating fixed panel */
.cart-panel {
    position: fixed;
    top: 60px;
    right: 20px;
    width: 360px;
    max-height: calc(100vh - 80px);
    background: white;
    box-shadow: 0 10px 35px rgba(90, 42, 131, 0.3);
    border-radius: 12px;
    padding: 1rem 1.25rem;
    display: none;
    flex-direction: column;
    z-index: 30;
    overflow-y: auto;
}
.cart-panel.active {
    display: flex;
}
.cart-panel h2 {
    color: #5a2a83;
    margin-bottom: 0.8rem;
    font-weight: 700;
    font-size: 1.3rem;
}
.cart-items {
    max-height: 180px;
    overflow-y: auto;
    margin-bottom: 1rem;
}
.cart-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.4rem;
    padding-bottom: 0.3rem;
    border-bottom: 1px solid #ddd;
}
.cart-item span {
    color: #5a2a83;
    font-weight: 600;
}
.cart-total {
    font-weight: 700;
    font-size: 1.2rem;
    margin-bottom: 1rem;
    color: #7f50be;
    text-align: right;
}

/* Form for user details */
form.user-details {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
}
form.user-details label {
    font-weight: 600;
    color: #5a2a83;
}
form.user-details input, form.user-details textarea {
    padding: 0.5rem 0.75rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    font-size: 1rem;
    outline: none;
    transition: border-color 0.3s ease;
}
form.user-details input:focus, form.user-details textarea:focus {
    border-color: #7f50be;
}
form.user-details textarea {
    resize: vertical;
}
.btn-confirm {
    margin-top: 0.75rem;
    background: #5a2a83;
    color: white;
    border: none;
    padding: 0.6rem 1rem;
    border-radius: 25px;
    font-weight: 700;
    cursor: pointer;
    align-self: flex-start;
    transition: background 0.3s ease;
}
.btn-confirm:hover {
    background: #7f50be;
}

/* Invoice */
.invoice-container {
    display: none;
    margin-top: 1rem;
    background: white;
    padding: 1rem 1rem 1.25rem 1rem;
    border-radius: 12px;
    box-shadow: 0 4px 25px rgba(90, 42, 131, 0.15);
}
.invoice-container.active {
    display: block;
}
.invoice-container h2 {
    margin-top: 0;
    font-weight: 700;
    color: #5a2a83;
    border-bottom: 2px solid #c293fc;
    padding-bottom: 0.3rem;
}
.invoice-section {
    margin-top: 1rem;
}
.invoice-section h3 {
    margin-bottom: 0.5rem;
    color: #7f50be;
}
.invoice-table {
    width: 100%;
    border-collapse: collapse;
}
.invoice-table th, .invoice-table td {
    padding: 0.5rem;
    border: 1px solid #ddd;
    text-align: left;
}
.invoice-table th {
    background: #c8b3ff;
    color: #5a2a83;
}
.print-btn {
    margin-top: 1rem;
    background: #5a2a83;
    color: white;
    border: none;
    padding: 0.7rem 1.2rem;
    border-radius: 25px;
    font-weight: 700;
    cursor: pointer;
    transition: background 0.3s ease;
}
.print-btn:hover {
    background: #7f50be;
}

/* Responsive adjustments */
@media (max-width: 900px) {
    .container {
        flex-direction: column;
        margin: 1rem;
    }
    .side-carousel {
        width: 100%;
        height: 220px;
        flex-direction: row;
        overflow-x: auto;
    }
    .carousel-image {
        height: 100%;
        max-width: 300px;
        flex-shrink: 0;
        border-radius: 0;
    }
    .main-content {
        padding: 1rem 0.5rem;
    }
    nav .search-container {
        max-width: 250px;
        margin: 0 1rem;
    }
    .cart-panel {
        width: 100vw;
        max-width: 100vw;
        height: 70vh;
        top: 50px;
        right: 0;
        border-radius: 0;
        max-height: none;
    }
}
